<template>
   <div class="BXliveSession__BxGroup__BXstudyData">
     <div class="BoxTabLable">
        <span
           v-for="(n, index) in LabelTab" :key="index"
           :class="{spanIsActive:index == current}"
           @click="current = index"
        >
           {{n}}
        </span>
     </div>
     <div class="BoxTabContent">
         <div v-show="current === 0" class="BXscripts">
           <div class="study__bottom live--sess">
             <CertCompletion />
             <PointsGoal :getWeeklyProgress="getWeeklyProgress" />
             <ProgressGoal />
             <ComprehensionGoal :getWeeklyProgress="getWeeklyProgress" />
             <PronunciationGoal :getWeeklyProgress="getWeeklyProgress" />
             <ListeningGoal :getWeeklyProgress="getWeeklyProgress" />
             <SpeakingGoal :getWeeklyProgress="getWeeklyProgress" />
           </div>
         </div>
         <div v-show="current === 2" class="BXscripts">
            <OpentokChat />
         </div>
    </div>
   </div>
</template>

<script>
  import CertCompletion from '../../../student-components/study-progress-report/cert-completion/CertCompletionWrapper'
  import ProgressGoal from '../../../student-components/study-progress-report/progress-goal/ProgressGoalWrapper'
  import PointsGoal from '../../../student-components/study-progress-report/bar-chart-point/PointsGoal'
  import ComprehensionGoal from '../../../student-components/study-progress-report/bar-chart-point/ComprehensionGoal'
  import PronunciationGoal from '../../../student-components/study-progress-report/bar-chart-point/PronunciationGoal'
  import ListeningGoal from '../../../student-components/study-progress-report/bar-chart-point/ListeningGoal'
  import SpeakingGoal from '../../../student-components/study-progress-report/bar-chart-point/SpeakingGoal'
  import OpentokChat from '../../../student-components/opentok-live-session/OpentokChat.vue'

   export default {
      components:{
        CertCompletion,
        ProgressGoal,
        PointsGoal,
        ComprehensionGoal,
        PronunciationGoal,
        ListeningGoal,
        SpeakingGoal,
        OpentokChat
      },
      props:[
         'DataUpcoming'
      ],
      data() {
         return {
           current:0,
           LabelTab:['STUDY DATA', 'CHAT'],
           AuthCoach: false
         }
      },
      mounted() {
         if(this.$store.state.UserRoleId === 'coach'){
            this.AuthCoach = true
            const studentUsername = {
               username: this.DataUpcoming[0].username
            }
            this.$store.dispatch('GET_CALL1_CALL2', studentUsername)
            console.log(studentUsername)

         } else if (this.$store.state.UserRoleId === 'student'){
            this.AuthCoach = false
            const studentUsername = {
               username: this.$store.state.DataAdditional.username
            }
            this.$store.dispatch('GET_CALL1_CALL2', studentUsername)
            console.log(studentUsername)
         }
      },
      created () {
        this.$store.dispatch('GET_STUDY_DASHBOARD')
        this.$store.dispatch('GET_WEEKLY_PROGRESS')
      },
      computed: {
        getWeeklyProgress () {
          const data = this.$store.state.stateWeeklyProgress
          if (data == null) {
            return 0
          } else {
            return data
          }
        }
      }
   }
</script>
<style lang="scss">
   @import '../../../views/study-progress-report/StudyProgressReport.scss';
</style>



